<template>
  <div class="redeem-container">
    <div class="logo-container">
      <img src="@/assets/images/seesawfilm_logo.png" alt="Seesaw Logo" class="logo-image" />
      <img src="@/assets/images/seesawfilm_title.png" alt="Seesaw Film Text" class="logo-text" />
    </div>
    <form @submit.prevent="redeemCode" class="redeem-form">
      <div class="form-group">
        <label for="code" :class="['seesun-body1-bold', 'sub-title']">리딤 코드</label>
        <input 
          type="text" 
          id="code" 
          v-model="code" 
          :class="{'error-input': isCodeError}, ['textfield-group']" 
          placeholder="코드를 입력해주세요" 
          required 
          class="input-field" 
        />
        <p v-if="isInvalidCode" class="error-message">*잘못된 코드입니다</p>
        <p v-if="isUsed" class="error-message">*이미 사용된 코드입니다</p>
        <p v-if="isOwned" class="error-message">*이미 보유하고 있는 필터입니다</p>
      </div>
      <div :class="{'form-group-second-invalid': isCodeError, 'form-group-second': !isInvalidCode}">
        <label for="userId" :class="['seesun-body1-bold', 'sub-title']">유저 ID</label>
        <p class="user-id-description">*유저 ID는 시소필름 홈 &gt; 설정 &gt; 프로필에서 확인할 수 있어요</p>
        <input 
          type="text" 
          id="userId" 
          v-model="userId" 
          :class="{'error-input': isIdError}, ['input-field']" 
          placeholder="유저 ID를 입력해주세요" 
          required 
          class="input-field" 
          
        />
        <p v-if="isIdError" class="error-message">*잘못된 유저 ID입니다</p>
      </div>
      <div :class="{'id-error-blank': !isIdError}"></div>
      <p v-if="isSucceed" :class="['seesun-body1-bold', 'message']">{{ message }}</p>
      <button 
        type="submit" 
        :disabled="!isFormValid" 
        :class="{'submit-button': true, 'submit-button-active': isFormValid}" 
      >
        등록하기
      </button>
    </form>
  </div>
</template>

<script>
import { db, collection, query, where, getDocs, updateDoc, doc, getDoc, addDoc, Timestamp } from '@/firebase';

export default {
  data() {
    return {
      code: '',
      userId: '',
      message: '',
      isInvalidCode: false,
      isSucceed: false,
      isOwned: false,
      isUsed: false,
      isCodeError: false,
      isIdError: false,
    };
  },
  computed: {
    // form이 유효한지 확인
    isFormValid() {
      return this.code !== '' && this.userId !== '';
    }
  },
  methods: {
   async redeemCode() {
        function mapFilterToSeries(filterName) {
            // 필터 이름이 특정 시리즈에 속하는지 매핑
            const seriesMapping = {
                "Yosigo": ["Ocean1", "Ocean2", "Ocean3", "Ocean4", "Ocean5", "Ocean6", "Ocean7", "Ocean8", "SanSebastian1"],
                "AWA": ["Antarctica", "Iceland", "NewZealand", "Portugal", "Scotland", "SouthAmerica", "Switzerland", "USA2", "USA3"],
                "Monet": ["Monet1", "Monet2", "Monet3", "Monet4", "Monet5", "Monet6", "Monet7", "Monet8", "Monet9"]
            };

            // 입력된 필터 이름이 어떤 시리즈에 속하는지 찾아서 반환
            for (const [seriesName, filters] of Object.entries(seriesMapping)) {
                if (filters.includes(filterName)) {
                    return seriesName;
                }
            }

            // 매핑된 시리즈가 없으면 null 반환
            return null;
        }
        try {
            // 리딤 코드가 입력된 'code' 필드와 일치하는 문서를 찾는 쿼리
            const q = query(
                collection(db, 'redeemCodes'),
                where('code', '==', this.code) // 'code' 필드와 입력된 값이 일치하는지 확인
            );

            const querySnapshot = await getDocs(q);

            if (!querySnapshot.empty) {
                const redeemDoc = querySnapshot.docs[0]; // 첫 번째 결과를 가져옴
                const data = redeemDoc.data();

                if (!data.isUsed) {
                    console.log("is not used");

                    // 유저 정보 가져오기
                    const userRef = doc(db, 'users', this.userId);
                    const userSnap = await getDoc(userRef);

                    if (userSnap.exists()) {
                        const userData = userSnap.data();
                        console.log("user is exsited")
                        let updateField = {};
                        // 리딤 코드의 type 필드에 따라 다른 필드를 업데이트
                        if (data.type === 'Filter') {
                            const series = mapFilterToSeries(data.itemName);
                            if (series) {
                                console.log(series);
                                const purchasesQuery = query(
                                    collection(db, `users/${this.userId}/purchases`),
                                    where('itemId', '==', series),
                                );
                                
                                const purchaseSnapshot = await getDocs(purchasesQuery);

                                // 클라이언트 측에서 가져온 도큐먼트를 배열로 변환하고 날짜로 정렬
                                const purchases = purchaseSnapshot.docs.map(doc => ({
                                    id: doc.id,
                                    ...doc.data()
                                }));

                                // purchaseDate 필드 기준으로 내림차순 정렬 (가장 최근 항목이 먼저 오도록)
                                purchases.sort((a, b) => b.purchaseDate.toDate() - a.purchaseDate.toDate());

                                // 가장 최근 항목 가져오기
                                const latestPurchase = purchases[0];

                                if (latestPurchase && !latestPurchase.isRefunded) {
                                    // 이미 필터가 포함되어 있는지 확인
                                    console.log("is not null"); 
                                    this.message = '이미 해당 필터를 보유하고 있습니다.';
                                    this.isCodeError = true;
                                    this.isSucceed = false;
                                    this.isInvalidCode = false;
                                    this.isOwned = true;
                                    this.isUsed = false;
                                    this.isIdError = false;
                                    return; // 오류 메시지를 출력하고 함수를 종료
                                  
                                } else {
                                    // Firestore에서 사용자의 purchases 중 itemName이 일치하는 항목
                                    const updatedFilters = userData.purchasedFilters || [];
                                    const purchasesQuery = query(
                                        collection(db, `users/${this.userId}/purchases`),
                                        where('itemId', '==', data.itemName),
                                    );

                                    
                                    const purchaseSnapshot = await getDocs(purchasesQuery);

                                    // 클라이언트 측에서 가져온 도큐먼트를 배열로 변환하고 날짜로 정렬
                                    const purchases = purchaseSnapshot.docs.map(doc => ({
                                        id: doc.id,
                                        ...doc.data()
                                    }));

                                    // purchaseDate 필드 기준으로 내림차순 정렬 (가장 최근 항목이 먼저 오도록)
                                    purchases.sort((a, b) => b.purchaseDate.toDate() - a.purchaseDate.toDate());

                                    // 가장 최근 항목 가져오기
                                    const latestPurchase = purchases[0];

                                    if (latestPurchase && !latestPurchase.isRefunded) {
                                        // 이미 필터가 포함되어 있는지 확인
                                        console.log("is not null"); 
                                        this.message = '이미 해당 필터를 보유하고 있습니다.';
                                        this.isCodeError = true;
                                        this.isSucceed = false;
                                        this.isInvalidCode = false;
                                        this.isOwned = true;
                                        this.isUsed = false;
                                        this.isIdError = false;
                                        return; // 오류 메시지를 출력하고 함수를 종료
                                      
                                    } else {
                                        console.log("is null");
                                    }
                                    // purchasedFilters 필드에 itemName 추가    
                                    updatedFilters.push(data.itemName);
                                    updateField.purchasedFilters = updatedFilters;
                                }
                            } 
                            
                        } else if (data.type === 'Series') {
                            // Firestore에서 사용자의 purchases 중 itemName이 일치하는 항목
                            const updatedSeries = userData.purchasedSeries || [];
                            
                            const purchasesQuery = query(
                                collection(db, `users/${this.userId}/purchases`),
                                where('itemId', '==', data.itemName),
                            );

                            const purchaseSnapshot = await getDocs(purchasesQuery);

                            // 클라이언트 측에서 가져온 도큐먼트를 배열로 변환하고 날짜로 정렬
                            const purchases = purchaseSnapshot.docs.map(doc => ({
                                id: doc.id,
                                ...doc.data()
                            }));

                            // purchaseDate 필드 기준으로 내림차순 정렬 (가장 최근 항목이 먼저 오도록)
                            purchases.sort((a, b) => b.purchaseDate.toDate() - a.purchaseDate.toDate());

                            // 가장 최근 항목 가져오기
                            const latestPurchase = purchases[0];

                            if (latestPurchase && !latestPurchase.isRefunded) {
                                this.message = '이미 해당 시리즈를 보유하고 있습니다.';
                                this.isCodeError = true;
                                this.isSucceed = false;
                                this.isInvalidCode = false;
                                this.isOwned = true;
                                this.isUsed = false;
                                this.isIdError = false;
                                return; // 오류 메시지를 출력하고 함수를 종료
                            
                            } else {
                              console.log("nullnull");
                            }
                            // purchasedSeries 필드에 itemName 추가
                            updatedSeries.push(data.itemName); 
                            updateField.purchasedSeries = updatedSeries;
                        }

                        // 유저 문서 업데이트
                        await updateDoc(userRef, updateField);

                        // purchases 하위 컬렉션에 새로운 문서 추가
                        const currencyMap = {
                            Korea: 'KRW',      // 대한민국 원
                            Japan: 'JPY',      // 일본 엔
                            Singapore: 'SGD'   // 싱가포르 달러
                        };
                        
                        const purchasesRef = collection(db, `users/${this.userId}/purchases`);
                        await addDoc(purchasesRef, {
                            itemId: data.itemName, // itemId로 itemName을 사용
                            purchaseDate: Timestamp.now(), // 현재 날짜와 시간
                            purchaseSource: data.codeType, // code의 타입
                            purchaseType: data.type === 'Filter' ? 'Filter' : 'Series', // 리딤 코드의 타입
                            redeemCode: this.code, // 사용한 리딤 코드
                            price: data.price,
                            currency: currencyMap[data.country] || 'USD',
                        });

                        this.isInvalidCode = false;
                        this.isOwned = false;
                        this.isUsed = false;
                        this.isSucceed = true;
                        this.isCodeError = false;
                        this.isIdError = false;
                        this.message = '코드 등록에 성공했습니다 !';

                        // 리딤 코드 사용 처리
                        await updateDoc(redeemDoc.ref, { isUsed: true });
                    } else {
                        this.isInvalidCode = false;
                        this.isOwned = false;
                        this.isUsed = false;
                        this.isSucceed = false;
                        this.isCodeError = false;
                        this.isIdError = true;
                        this.message = 'User not found!';
                    }
                } else {
                    this.isUsed = true;
                    this.isInvalidCode = false;
                    this.isOwned = false;
                    this.isSucceed = false;
                    this.isCodeError = true;
                    this.isIdError = false;
                    this.message = '이미 사용된 코드입니다.';
                }
            } else {
                this.isInvalidCode = true;
                this.isUsed = false;
                this.isOwned = false;
                this.isSucceed = false;
                this.isCodeError = true;
                this.isIdError = false;
                this.message = 'Invalid redeem code.';
            }
        } catch (error) {
            this.message = 'Error redeeming code: ' + error.message;
        }
    }
  }
};
</script>

<style scoped>

