  background-color: #0C0F14;
  color: #ffffff;
  padding: 20px;
  margin-top: 24px;
  width: 90vw;
  max-width: 1200px;
  text-align: left;
}

.redeem-form {
  display: flex;
  flex-direction: column;
}

