  display: flex;
  flex-direction: column;
  align-items: start;
  margin-bottom: 16px;
}

.logo-image {
  width: 90px; /* 로고 이미지 크기 */
  height: 90px;
}

.logo-text {
  width: 160px; /* 텍스트 이미지 크기 */
  margin-top: 24px;
  height: auto;
}

