  margin-top: 8px;
}

.user-id-description {
  font-size: 12px;
  color: #ffffff;
  margin-bottom: 8px;
  margin-top: 8px;
  text-align: left;
}

.input-field {
  width: calc(100% - 24px);
  height: 50px;
  padding-left: 16px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #383B40;
  background-color: #0C0F14;
  color: #ffffff;
}

.input-field.error-input {
  border-color: #FF6D6D;
}

.input-field:focus {
  outline: none;
  border-color: #ffffff;
}

.input-field::placeholder {
  color: #ffffff; /* 원하는 색상으로 변경 */
  opacity: 1;
}

.id-error-blank {
    height: 33px;
}


.submit-button {
  padding: 15px;
  width: calc(100%);
  max-width: 1200px;
  font-size: 18px;
  background-color: #0C0F14;
  color: #ffffff;
  border: 1px solid #A6ADB7;
  border-radius: 27px;
  cursor: not-allowed;
  position: relative;
  margin-top: 124px;
}

/* 모바일 환경일 때 (max-width: 768px 이하) */
@media (max-width: 768px) {
  .submit-button {
    position: fixed;
    left: 20px;
    bottom: 60px; /* 화면 하단에서 20px 위에 고정 */
    width: calc(100% - 40px); /* 양쪽에 20px씩 여백 */
  }
}

.submit-button-active {
  background-color: #ffffff;
  color: #000000;
  cursor: pointer;
}

.submit-button-active:hover {
  background-color: #dddddd;
}

